import { FaShoppingCart } from "react-icons/fa";
import Container from "../../containers/Container";
import FabPlus from "../../components/FabPlus";
import TransactionCard from "./components/TransactionCard";
import { getTransactions } from "../../api";
import { useEffect, useState } from "react";
import SkeletonLoading from "./components/SkeletonLoading";

const Transaction = (props) => {
  const [transactions, setTransactions] = useState([]);
  const [loadingApi, setLoadingApi] = useState(true);

  const loadData = async () => {
    setLoadingApi(true);
    try {
      const res = await getTransactions();
      setTransactions(res.data);
    } catch (err){}
    setLoadingApi(false);
  }

  useEffect(() => {
    loadData();
  }, []);

  return (
    <Container icon={FaShoppingCart} title="Transaction">
      <div className="main-content">
        <input 
          type="search" 
          className="form-control form-control-sm" 
          placeholder="search..."  
        />
        <hr className="my-2"/>
        {loadingApi ? [1,2,3,4,5].map(i => <SkeletonLoading key={i} />) : transactions.map(transaction => (
          <TransactionCard key={transaction.id} transaction={transaction} {...props} />
        ))}
      </div>
      <FabPlus 
        onClick={() => props.history.push("/transaction/create")}
      />
    </Container>
  )
}

export default Transaction;