import { useState } from "react";
import { MdKeyboardArrowRight, MdRadioButtonChecked, MdRadioButtonUnchecked } from "react-icons/md";
import NavTop from "../../components/NavTop";

const TransactionEdit = (props) => {
  const [transactionType, setTransactionType] = useState(1);
  return (
    <>
      <NavTop title="Edit Transaction" />
      <div className="main-content">
        <div className="form-group">
          <label>Pilih Wallet</label>
          <div className="form-control form-control-sm cursor-pointer align-items-center d-flex justify-content-between" onClick={() => props.history.push("/wallet?type=select")}>
            <span></span>
            <MdKeyboardArrowRight />
          </div>
        </div>
        <div className="form-group">
          <label>Total Transaksi</label>
          <input placeholder="Rp." type="number" className="form-control form-control-sm"/>
        </div>
        <div className="form-group">
          <label>Tanggal</label>
          <input type="date" className="form-control form-control-sm"/>
        </div>
        <div className="form-group">
          <label>Jenis Transaksi</label>
          <div className="d-flex">
            <div className="p-2 bg-light d-flex align-items-center shadow-sm cursor-pointer text-dark mr-2" onClick={() => setTransactionType(1)} >
              {transactionType === 1 ? 
                <MdRadioButtonChecked /> :
              <MdRadioButtonUnchecked />}
              <small style={{fontSize: '.8em'}} className="ml-2">Incoming Transaction</small>
            </div>
            <div className="p-2 bg-light d-flex align-items-center shadow-sm cursor-pointer text-dark" onClick={() => setTransactionType(0)} >
              {transactionType === 0 ?
                <MdRadioButtonChecked /> :
              <MdRadioButtonUnchecked />}
              <small style={{fontSize: '.8em'}} className="ml-2">Outcoming Transaction</small>
            </div>
          </div>
        </div>
        <div className="form-group">
          <label>Catatan</label>
          <textarea rows="5" className="form-control form-control-sm" style={{resize: 'none'}}></textarea>
        </div>
        <div className="form-group">
          <button className="btn btn-primary btn-block btn-sm">Save</button>
        </div>
      </div>
    </>

  )
}

export default TransactionEdit;